import { Component, OnInit, ViewChild, EventEmitter, Output, Input} from '@angular/core';
import { BOTreePanelState} from './bo-tree-panel.state';
import { BusinessObjectRtService } from '@gsp-lcm/bo-rt-service';
import { TreeTableComponent } from '@farris/ui-treetable'; 
// è®¾ç½®éè¦æ¾ç¤ºçå¼
const CUSTOM_TEMPLATE_STRING = '{{ node.name }} ({{ node.subTitle }})';

@Component({
  selector: 'app-bo-tree-panel',
  templateUrl: './bo-tree-panel.component.html',
  styleUrls: ['./bo-tree-panel.component.css'],
})

export class BOTreePanelComponent implements OnInit {
  @Input() showIcon : boolean;
  @Output() selectionChanged = new EventEmitter<any>();
  @ViewChild('treetable') treetable : TreeTableComponent;
  cols = [
    { field: 'name', title: 'Name', width: 200}
  ];
  // è·åæ°æ®
  treeNodes: any[];
  selctedBO: any;
  customTemplateStringOptions = { treeNodeTemplate: CUSTOM_TEMPLATE_STRING };
  
  // æ³¨å¥æä»¶æ æ°æ®è·åæå¡ï¼ä»¥è·åæ°æ®
  constructor(public boRtService: BusinessObjectRtService, private boTreePanelState: BOTreePanelState) {
    this.getInitBOTreeData();
  }

  /**
   * è·åæ ¹ç®å½æ 
   */
  getInitBOTreeData() {
    this.boRtService.getBusinessObjects().subscribe((res: any) => {
      if (res instanceof Array) {
        res.forEach((item) => {// kendoUi treeview parentId ä¸ºç¬¬ä¸çº§çæ¶å å¿é¡»undefinedï¼å¿é¡»åwebapiå¾å°å®ä½å¤§å°åä¸è´
          if (item.parentID===undefined || item.parentID === null || item.parentID === '') {
            item.parentID = undefined;
          }
          if (item.sysInit === '1') {
            item.isSysInit = true;
          } else {
            item.isSysInit = false;
          }
        });
      } else {
        if (res.parentID===undefined || res.parentID === null || res.parentID === '') {
          res.parentID = undefined;
          if (res.sysInit === '1') {
            res.isSysInit = true;
          } else {
            res.isSysInit = false;
          }
        }
      }
      this.treeNodes =this.makeTree(res);
    });
  }

  selectNode(id: string, detectChanges?: boolean){
    this.treetable.selectNode(id,detectChanges);
  }
  


  doubleClickEventHandler($event) {
        this.boTreePanelState.boTreeNodeDoubleClick.emit($event.node);
  }


  ngOnInit() {
  }
  selectionChange($event) {
    this.selctedBO = $event.node.data;
    this.selectionChanged.emit($event.node);
  }

  refresh() {
    this.getInitBOTreeData();
  }
  private makeTree(data) {
    const r = data.filter(t => t.layer === 1).map(t => {
        return {
            id:t.id,
            data: t,
            children: [],
            expanded: true
        };
    });

    r.forEach(e => {
        e.children = this.makeTreeChildren(data, e.data);
    });

    return r;
  }   
  private makeTreeChildren(data, treeInfo) {
    const pLayer = treeInfo.layer + 1;      

    return data.filter( c => c.layer === pLayer && c.parentID===treeInfo.id).map( d => {
      return {
          data: d,
          children: this.makeTreeChildren(data, d),
          expanded: false
      };
    });
  }
}
